import React, { useState, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { tripApi } from '../api/api';
import './NewTripPage.css'; //   

//     
const regionOptions = [
  { name: '', lat: 37.5665, lng: 126.9780 },
  { name: '', lat: 37.4138, lng: 127.5183 },
  { name: '', lat: 37.4563, lng: 126.7052 },
  { name: '', lat: 37.8228, lng: 128.1555 },
  { name: '', lat: 36.8000, lng: 127.7000 },
  { name: '', lat: 36.5184, lng: 126.8000 },
  { name: '', lat: 36.3504, lng: 127.3845 },
  { name: '', lat: 35.8242, lng: 127.1489 },
  { name: '', lat: 34.8679, lng: 126.9910 },
  { name: '', lat: 35.1595, lng: 126.8526 },
  { name: '', lat: 36.4919, lng: 128.8889 },
  { name: '', lat: 35.4606, lng: 128.2132 },
  { name: '', lat: 35.8714, lng: 128.6014 },
  { name: '', lat: 35.5384, lng: 129.3114 },
  { name: '', lat: 35.1796, lng: 129.0756 },
  { name: '', lat: 33.4996, lng: 126.5312 }
];

const EditTripPage = () => {
  const { tripId } = useParams();
  const navigate = useNavigate();
  const [loading, setLoading] = useState(true);
  const [formData, setFormData] = useState({
    title: '',
    startDate: '',
    endDate: '',
    description: '',
    region: '',
    regionLat: '',
    regionLng: ''
  });

  useEffect(() => {
    const fetchTripDetail = async () => {
      try {
        const response = await tripApi.getTripDetail(tripId);
        const tripData = response.data;

        //       
        setFormData({
          title: tripData.title || '',
          startDate: tripData.startDate || '',
          endDate: tripData.endDate || '',
          description: tripData.description || '',
          region: tripData.region || '',
          regionLat: tripData.regionLat || '',
          regionLng: tripData.regionLng || ''
        });
        
        setLoading(false);
      } catch (err) {
        console.error('   :', err);
        alert('    .');
        navigate('/');
      }
    };

    fetchTripDetail();
  }, [tripId, navigate]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: value
    });
  };

  const handleRegionChange = (e) => {
    const selectedRegionName = e.target.value;
    const selectedRegion = regionOptions.find(region => region.name === selectedRegionName);
    
    if (selectedRegion) {
      setFormData({
        ...formData,
        region: selectedRegion.name,
        regionLat: selectedRegion.lat,
        regionLng: selectedRegion.lng
      });
    } else {
      setFormData({
        ...formData,
        region: '',
        regionLat: '',
        regionLng: ''
      });
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!formData.title || !formData.startDate || !formData.endDate || !formData.region) {
      alert(',  ,    .');
      return;
    }

    try {
      setLoading(true);
      await tripApi.updateTrip(tripId, formData);
      alert('   !');
      navigate(`/trips/${tripId}`);
    } catch (err) {
      console.error('  :', err);
      alert('    .   .');
    } finally {
      setLoading(false);
    }
  };

  if (loading) return <div className="loading"> ...</div>;

  return (
    <div className="new-trip-container">
      <h2>  </h2>
      <form className="trip-form" onSubmit={handleSubmit}>
        <div className="form-group">
          <label htmlFor="title"> </label>
          <input
            type="text"
            id="title"
            name="title"
            value={formData.title}
            onChange={handleChange}
            placeholder="  "
            required
          />
        </div>
        
        <div className="form-group">
          <label htmlFor="region"> </label>
          <select
            id="region"
            name="region"
            value={formData.region}
            onChange={handleRegionChange}
            required
          >
            <option value=""> </option>
            {regionOptions.map(region => (
              <option key={region.name} value={region.name}>
                {region.name}
              </option>
            ))}
          </select>
        </div>
        
        <div className="form-row">
          <div className="form-group">
            <label htmlFor="startDate"></label>
            <input
              type="date"
              id="startDate"
              name="startDate"
              value={formData.startDate}
              onChange={handleChange}
              required
            />
          </div>
          
          <div className="form-group">
            <label htmlFor="endDate"></label>
            <input
              type="date"
              id="endDate"
              name="endDate"
              value={formData.endDate}
              onChange={handleChange}
              required
            />
          </div>
        </div>
        
        <div className="form-group">
          <label htmlFor="description">  ()</label>
          <textarea
            id="description"
            name="description"
            value={formData.description || ''}
            onChange={handleChange}
            placeholder="    "
            rows="3"
          />
        </div>
        
        <div className="form-actions">
          <button 
            type="button" 
            className="cancel-button"
            onClick={() => navigate(`/trips/${tripId}`)}
          >
            
          </button>
          <button 
            type="submit" 
            className="submit-button"
            disabled={loading}
          >
            {loading ? ' ...' : ' '}
          </button>
        </div>
      </form>
    </div>
  );
};

export default EditTripPage; 